.results {
  width: 90%;
  margin: 20px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }
  .results .images,
  .results .pages {
    display: flex;
    flex-direction: column;
    padding: 10px;
    width: 45%; }
    .results .images .results,
    .results .pages .results {
      display: flex;
      width: 100%; }
    .results .images .page-result-info,
    .results .pages .page-result-info {
      text-align: center;
      font-size: small; }
    .results .images i,
    .results .pages i {
      align-self: center; }
  .results .pages .results {
    flex-direction: column;
    font-family: Arial, sans-serif; }
    .results .pages .results .page-result {
      width: 100%; }
      .results .pages .results .page-result .title {
        font-size: 18px;
        color: #004cb1; }
        .results .pages .results .page-result .title a {
          text-decoration: none; }
          .results .pages .results .page-result .title a:hover {
            text-decoration: underline; }
          .results .pages .results .page-result .title a:visited {
            color: #609; }
      .results .pages .results .page-result .link a {
        font-size: small;
        color: grey; }
      .results .pages .results .page-result .snippet p {
        font-size: small;
        line-height: 0.75rem;
        margin-top: 4px; }
  .results .images .results {
    max-height: 750px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around; }
    .results .images .results .image-result {
      box-shadow: 0px 0px 10px #686868;
      width: 30%;
      max-height: 180px;
      margin: 7px;
      display: flex;
      justify-content: space-around;
      align-items: center; }
      .results .images .results .image-result:hover {
        box-shadow: 0px 0px 20px #686868; }
      .results .images .results .image-result .thumbnail,
      .results .images .results .image-result img {
        width: 100%;
        max-height: 160px; }
      .results .images .results .image-result .size-link {
        display: none; }

#buttons {
  display: flex;
