body.landing {
  margin: 0; }

.search-container {
  box-shadow: 0px 0px 10px #686868;
  display: flex;
  align-items: center; }
  .search-container:hover {
    box-shadow: 0px 0px 20px #686868; }
  .search-container .logo {
    background: linear-gradient(to bottom, #0089F3 0%, #0062E4 100%);
    display: flex;
    justify-content: space-around;
    align-items: center; }
    .search-container .logo h1 {
      text-align: center;
      font-family: Futura, sans-serif;
      padding: 0 10px;
      font-weight: 300;
      font-size: 2rem;
      color: white; }
  .search-container .search {
    background: white;
    width: 100%; }
    .search-container .search .input {
      margin: 0 10px;
      border-left: 2px solid #0089F3;
      border-right: 2px solid #0089F3;
      padding: 0 8px;
      transition: all 0.5s ease;
      display: flex;
      justify-content: space-around;
      align-items: center; }
      .search-container .search .input button {
        visibility: hidden; }
      .search-container .search .input i {
        font-size: 3em; }
      .search-container .search .input.open i {
        color: #0062E4; }
      .search-container .search .input.open input {
        border: 0px solid grey;
        border-bottom-width: 1px;
        font-size: 1em;
        flex-basis: 100%;
        margin: 0 10px; }
        .search-container .search .input.open input:focus {
          outline: none;
          border: 0px solid #0089F3;
          border-bottom-width: 2px; }
      .search-container .search .input.open button {
        margin-right: 0;
        visibility: visible;
        flex-basis: 20%;
        background-color: #0089F3;
        border-width: 0;
        border-radius: 5px;
        transition: all 0.2s ease-in;
        cursor: pointer; }
        .search-container .search .input.open button i {
          color: white; }
        .search-container .search .input.open button:hover {
          background-color: #0062E4; }

.content {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center; }
  .content .search-container {
    align-items: stretch;
    width: 50%;
    height: 40%;
    max-height: 220px;
    flex-direction: column; }
    .content .search-container .logo {
      height: 80%; }
      .content .search-container .logo h1 {
        font-weight: 400;
        font-size: 4rem; }
    .content .search-container .search {
      height: 20%;
      align-self: stretch;
      display: flex;
      justify-content: space-around;
      align-items: center; }
      .content .search-container .search .input {
        position: relative;
        width: 10%; }
        .content .search-container .search .input input,
        .content .search-container .search .input button {
          width: 0;
          visibility: hidden;
          border-width: 0; }
        .content .search-container .search .input i {
          position: absolute;
          color: #0089F3;
          font-size: 1.75em;
          z-index: 10; }
          .content .search-container .search .input i:hover {
            cursor: pointer;
            color: #0062E4; }
        .content .search-container .search .input.open {
          transition: all 0.5s ease;
          border-color: #0062E4;
          width: 90%;
          display: flex; }
          .content .search-container .search .input.open i {
            position: static; }
          .content .search-container .search .input.open input {
            visibility: visible;
            flex-basis: 100%; }
          .content .search-container .search .input.open button {
            visibility: visible; }
